<template>
  	<el-row :gutter="20" class=" stepSec container">
			  <el-col :span="12" :offset="7">
				  	<el-main>
				  		<div>
			  				<el-form :model="sizeForm2" status-icon ref="sizeForm2" label-width="100px" class="demo-ruleForm">
			  				 <el-form-item label="">
							    <el-radio-group v-model="sizeForm2.resource" size="medium" @change="changeHandler">
							      <el-radio label="0">个人注册</el-radio>
							      <el-radio  label="1">组织机构注册</el-radio>
							    </el-radio-group>
							  </el-form-item>
							 </el-form> 
				  		</div>
				    	<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
							  <el-form-item label="行业" prop="industry">
							   		<el-select v-model="ruleForm2.industry" placeholder="请选择行业">
									    <el-option
									      v-for="item in options"
									      :key="item.value"
									      :label="item.label"
									      :value="item.value">
									    </el-option>
									</el-select>
							  </el-form-item>
							    <template v-if="status == 1" >
							    	<el-form-item label="组织机构类型" prop="orgType">
								   		<el-select v-model="ruleForm2.orgType" placeholder="请选择组织机构类型">
										    <el-option
										      v-for="item in orgOptions"
										      :key="item.value"
										      :label="item.label"
										      :value="item.value">
										    </el-option>
										</el-select>
									</el-form-item>
									  <el-form-item label="机构名称" prop="orgName">
									     <el-input type="text" v-model="ruleForm2.orgName" class="cl-input" auto-complete="off"></el-input>
									  </el-form-item>
									  <el-form-item label="机构代码" prop="orgCode">
									     <el-input type="text" v-model="ruleForm2.orgCode" class="cl-input"  auto-complete="off"></el-input>
									  </el-form-item>
								</template>  
							  <el-form-item label="地址" prop="province">
							     <el-cascader :options="addrOptions" v-model="ruleForm2.province" @change="getVal" ></el-cascader>
							  </el-form-item> 
							  <el-form-item label="" prop="address">
							  	<el-input type="text" v-model="ruleForm2.address" class="fl cl-input ml30" auto-complete="off" placeholder="请填入街道地址"></el-input>
							  </el-form-item> 	
							  <el-form-item label="联系电话" prop="phone">
							     <el-input type="text" v-model="ruleForm2.phone" class="cl-input"  auto-complete="off"></el-input>
							  </el-form-item>
							  <el-form-item label="联络人姓名" prop="lxrName">
							     <el-input type="text" v-model="ruleForm2.lxrName" class="cl-input" auto-complete="off"></el-input>
							  </el-form-item>
							   <el-form-item><!-- <router-link to="/register/stepSec" class="a-link"> -->
							   	<el-button type="primary" @click="register('ruleForm2')" class="btn0">注&emsp;册</el-button>
							  </el-form-item>
							</el-form>
					</el-main>	
		  </el-col>
		</el-row>
</template>
<script>
export default {
  data() {
      var checkPhone = (rule, value, callback) => {
      	if (!value.match(/\S/)) {
          return callback(new Error('手机号码不能为空'));
        } 
        if(!value.match(/^((\+86\-?)|(\(\+86\)))?1[34578][0-9]{9}$/)) {
          return callback(new Error('请填写正确的手机号码'));
        }
		return callback();
      };
      
      var checkLxrName = (rule, value, callback) => {
      	if (!value.match(/\S/)) {
          return callback(new Error('联系人不能为空'));
        } 
        if(!value.match(/^[\u9fa5a-zA-Z0-9\-\_\\()]{4,20}$/)) {
          return callback(new Error('4-20个字符，支持文字、英文（区分大小写），支持数字“_” “-”、()的组合'));
        }
		return callback();
      }
      
      return {
      	 status: 0,
      	 flag: true,
      	 options: [{
	          value: '001',
	          label: '教育'
	        }, {
	          value: '002',
	          label: '医疗'
	        }, {
	          value: '003',
	          label: '金融'
	        }, {
	          value: '004',
	          label: 'IT'
	        }, {
	          value: '005',
	          label: '政府'
	        }],
	        value: '',
	        orgOptions: [{
	          value: '001',
	          label: '中学'
	        }],
	        orgValue: '',
	        addrOptions: [],
	        props: {
	          value: 'label',
	          children: 'cities'
	        },
        sizeForm2: {
          resource: '0'
        },
	    ruleForm2: {
          industry: '',
          orgType: '',
          orgCode: '',
          orgName: '',
          address: '',
          province: [],
          phone: '',
          lxrName: ''
        },
        rules2: {
          industry: [{ required: true, message: '请选择行业', trigger: 'change' }],
          orgType: [{ required: true, message: '请选择组织类型', trigger: 'change' }],
          orgCode: [{required: true, message: '请选择组织机构代码', trigger:'blur'}],
          orgName: [{ required: true, message: '请选择组织机构名称', trigger: 'blur' }],
          province: [{ required: true, message: '请选择省市区', trigger: 'change' }],
          address: [{ required: true, message: '请填写地区', trigger: 'change' }],
          phone: [{ validator: checkPhone, trigger: 'blur' }],
          lxrName: [{ validator: checkLxrName, trigger:'blur' }]
        }
      };
	},
	methods: {
		getVal(value) {
			console.log(value)
		},
		changeHandler(value) {
			this.status = parseInt(value);
			this.$refs.ruleForm2.resetFields();
			if(value == 0) {
				this.flag = false;
			} else {
				this.flag = true;
			}
		},
		register(formName) {
			this.$refs[formName].validate((valid) => {
	       		console.log("valid==> ", valid)
	          if (valid) {
	            alert('submit!');
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
		},
		handleItemChange(val) {
	        setTimeout(_ => {
	          if (val.indexOf('江苏') > -1 && !this.addrOptions[0].cities.length) {
	            this.addrOptions[0].cities = [{
	              label: '南京'
	            }];
	          } else if (val.indexOf('浙江') > -1 && !this.addrOptions[1].cities.length) {
	            this.addrOptions[1].cities = [{
	              label: '杭州'
	            }];
	          }
	        }, 300);
	      }
	 	},
		 created() {
	      	this.addrOptions =  this.CityInfo
		 },
		 mounted() {},
	    beforeRouteEnter(to, from, next) {next()},
		 beforeRouteUpdate(to, from, next) {next()},
		 beforeRouteLeave(to, from, next) {next()}
	}
</script>


